import React, { Component } from 'react';
import PropTypes from 'prop-types';

import './HighScoreInput.css';
import { saveHOEntry } from './HallOfFame';

class HighScoreInput extends Component {
	state = { winner: '' };

	// Arrow function for bidding
	// on utilise l'initialiseur pour pouvoir passer par r√©f√©rence
	handleWinnerUpdate = event => {
		this.setState({ winner: event.target.value.toUpperCase() });
	};

	// Arrow function for bidding
	// on utilise l'initialiseur pour pouvoir passer par r√©f√©rence
	persistWinner = event => {
		// emp√™cher le comportement par d√©fault de la soumission, cad la navigation compl√®te vers un serveur
		event.preventDefault();
		const newEntry = {
			guesses: this.props.guesses,
			player: this.state.winner
		};
		// onStored est une fonction de rappel (callback)
		saveHOEntry(newEntry, this.props.onStored);
	};
	render () {
		return (
			<form className="highScoreInput" onSubmit={this.persistWinner}>
				<p>
					<label>
						Bravo ! Entrez ton pr√©nom ou Pressez ENTREE pour une nouvelle partie
						<input
							type="text"
							autoComplete="given-name"
							value={this.state.winner}
							onChange={this.handleWinnerUpdate}
						/>
					</label>
					<button type="submit">J'ai gagn√© !</button>
				</p>
			</form>
		);
	}
}

HighScoreInput.propTypes = {
	guesses: PropTypes.number.isRequired,
	onStored: PropTypes.func.isRequired
};

export default HighScoreInput;
